}
.custom-class .slide-pane__header{
 background-color:  "#1D5BA3";
}
body {
 -webkit-user-select: none; 
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none; 
}
@keyframes opacityCircle {
  0%, 100% {
      opacity: 0.3; /* Giảm độ trong suốt ban đầu */
  }
  50% {
      opacity: 1; /* Tăng độ trong suốt lên 1 giữa animation */
  }
}

@keyframes opacityPulse {
  0%, 100% {
      opacity: 0.5; /* Giảm độ trong suốt ban đầu */
  }
  50% {
      opacity: 1; /* Tăng độ trong suốt lên 1 giữa animation */
  }
}

@keyframes shakeIcon {
  0%, 100% {
      transform: rotate(0deg); 
  }
  50% {
      transform: rotate(20deg);
  }
}

.cmoz-alo-circle {
  animation: opacityCircle 0.8s infinite; 
}

.pulse_zalo {
  animation: opacityPulse 0.8s infinite;
}

.animated_zalo {
  animation: shakeIcon 0.8s ease-in-out infinite;
}
